<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="anträge"
      item-key="name"
      class="elevation-1 ma-5"
      :search="search"
    >
      <template v-slot:top>
        <v-text-field
          v-model="search"
          label="Nach Firma suchen..."
          class="mx-4"
        ></v-text-field>
      </template>
      <template v-slot:body.append>
        <tr>
          <td></td>
          <td>
            <v-text-field
              v-model="calories"
              type="number"
              label="Less than"
            ></v-text-field>
          </td>
          <td colspan="4"></td>
        </tr>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      search: '',
      calories: '',
      anträge: [],
    };
  },
  computed: {
    headers() {
      return [
        {
          text: 'Name',
          align: 'start',
          value: 'firmen_name',
        },
        {
          text: 'Email',
          value: 'firmen_mail',
        },
        { text: 'Anfrage Zeitpunkt', value: 'anfrage_zeitpunkt' },
        { text: 'Status', value: 'status' },
      ];
    },
  },
  created() {
    this.getAnträge();
  },
  methods: {
    async getAnträge() {
      const { data } = await axios({
        url: '/antraege',
        method: 'GET',
      });
      this.anträge = data;
    },
  },
};
</script>

<style lang="scss" scoped></style>
