<template>
  <v-container style="height: 100%" class="mb-10">
    <!-- @submit.prevent="login" -->
    <v-row align="center" justify="center">
      <v-col cols="12" md="6">
        <div class=" text-center ">
          <img src="@/assets/Anmeldung_Logo.jpg" width="70%" />
        </div>
      </v-col>

      <v-col cols="12" md="6">
        <v-row class="px-3">
          <label class="pl-0 mb-1">
            <h6 class="mb-0 text-sm">User-ID</h6>
          </label>
          <input
            class="logininput"
            style="margin-left: 0"
            type="text"
            @keyup.enter="login()"
            name="email"
            v-model="userId"
            placeholder="Enter a valid email address"
          />
          <label class="pl-0 mb-1">
            <h6 class="  text-sm" style="color:red">
              {{ message }}
            </h6>
          </label>
        </v-row>
        <v-row class="px-3 mb-4 mt-4">
          <label class="pl-0 mb-1">
            <h6 class=" mb-0 text-sm">Password</h6>
          </label>
          <input
            class="logininput"
            type="password"
            name="password"
            @keyup.enter="login()"
            v-model="passwort"
            placeholder="Enter password"
          />
          <label class="pl-0 mb-1">
            <h6 class=" mb-0 text-sm" style="color:red">
              {{ message }}
            </h6>
          </label>
        </v-row>
        <v-checkbox
          v-model="angemeldetBleiben"
          label="Angemeldet bleiben"
          value="true"
        ></v-checkbox>
        <!-- <div class="row px-3 mb-4">
                <a href="#" class="ml-auto mb-0 text-sm">Forgot Password?</a>
              </div> -->
        <v-btn
          @click="login()"
          class="white--text red darken-4"
          style="border-radius: 0; width: 5vw"
        >
          Login
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import axios from 'axios';
export default {
  name: 'app',
  data() {
    return {
      userId: '',
      passwort: '',
      user: {},
      message: '',
      angemeldetBleiben: false,
    };
  },
  components: {},
  created() {
    let user = JSON.parse(localStorage.getItem('user'));
    if (user != null) {
      this.user = user;
    }
  },
  methods: {
    async login() {
      try {
        let { data } = await axios({
          url: '/login',
          method: 'POST',
          contentType: 'application/json',
          data: {
            userId: this.userId,
            passwort: this.passwort,
          },
        });
        if (
          this.angemeldetBleiben == 'false' ||
          this.angemeldetBleiben == false
        ) {
          localStorage.setItem(
            'loginBleiben',
            JSON.stringify({ login: false }),
          );
        } else if (
          this.angemeldetBleiben == 'true' ||
          this.angemeldetBleiben == true
        ) {
          localStorage.setItem('loginBleiben', JSON.stringify({ login: true }));
        }

        localStorage.setItem('user', JSON.stringify(data));

        this.$router.push('/');
        this.$router.go();
      } catch (err) {
        this.message = 'Wrong User ID or password';
      }
    },
  },
};
</script>
<style scoped>
.text-sm {
  font-size: 14px !important;
}

::placeholder {
  color: #bdbdbd;
  opacity: 1;
  font-weight: 300;
}

:-ms-input-placeholder {
  color: #bdbdbd;
  font-weight: 300;
}

::-ms-input-placeholder {
  color: #bdbdbd;
  font-weight: 300;
}

.logininput {
  padding: 10px 12px 10px 12px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  margin-bottom: 5px;
  margin-top: 2px;
  width: 100%;
  box-sizing: border-box;
  color: #2c3e50;
  font-size: 14px;
  letter-spacing: 1px;
}

.logininput:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  border: 1px solid #304ffe;
  outline-width: 0;
}
</style>
